<template>
  <!-- モーダルテンプレ -->
  <!-- <script type="text/x-template" id="explanatory-image-modal-template">
  <transition name="modal" > -->
    <div class="modal-mask" v-on:keyup.esc="$emit('close')">
      <div class="modal-wrapper">
        <div class="modal-container" >
          <div class="modal-header">
            <button
              class="modal-designed-modal-header-image"
              v-for="imageAndExplain in imageAndExplains"
              :key="imageAndExplain.imageName"
              @click="selectImage(imageAndExplain)"
            >
              <img :src="imageAndExplain.imageName" alt />
            </button>
            <button type="button" class="modal-designed-modal-header-image close" style="height:100px; width:100px;" @click="close">×</button>
          </div>

          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <p v-html="selectedExplanation">{{ selectedExplanation }}</p>
              </div>
              <div class="col-sm-8 modal-designed-modal-right">
                <img
                  class="center-block modal-designed-modal-right-image"
                  v-bind:src="selectedImageName"
                  alt
                />
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  <!-- </transition>
  </script> -->
</template>

<script>
export default {
  props: {
    initImages: {
      type: Array,
      required: true
    }
  },
  data: function () {
    return {
      imageAndExplains: this.initImages,
      selectedImageName: this.initImages[0].imageName,
      selectedExplanation: this.initImages[0].explanation
    }
  },
  mounted: function () {
  },
  methods: {
    selectImage: function (image) {
      this.selectedImageName = image.imageName
      this.selectedExplanation = image.explanation
    },

    close: function () {
      this.$emit('close')
    }
  }
}
</script>

<style lang="scss" scoped>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 90vw;
  height: 90vh;
  margin: 0px auto;
  padding: 20px 0px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header {
  width: 100%;
  min-width: 100%;
  padding: 0;
  overflow-x: auto;

  img {
    object-fit: contain;
    width: 100px;
    height: 100px;
  }

  div {
    display: flex;
    margin: 0 auto;
  }
}

.modal-body {
  width: 100%;
  height: 90%;
  margin: 0px auto;
}

.modal-designed-modal-header-image {
  margin: 0 3px;
}
.modal-designed-modal-right {
  height: 100%;
}
.modal-designed-modal-right-image {
  object-fit: contain;
  width: 100%;
  max-height: 73vh;
}
</style>



