<template>
  <div id="overlay">
    <div id="content">
      <p>これがモーダル</p>
      <p>
        <button @click="close">close</button>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
    }
  },
  methods: {
    close: function () {
      this.$emit('close')
    }
  }
}
</script>

<style lang="scss" scoped>
#overlay {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);

  display: flex;
  align-items: center;
  justify-content: center;
}

#content {
  z-index: 2;
  width: 50%;
  padding: 1rem;
  background: #fff;
}
</style>



