<template>
  <div class="modal-mask" v-on:keyup.esc="$emit('close')">
    <div class="modal-wrapper">
      <div class="modal-container">
        <div class="modal-header">
          <button
            class="modal-designed-modal-header-image"
            v-for="media in medias"
            :key="media.path"
            @click="selectMedia(media)"
          >
            <img v-if="media.type === 'image'" :src="media.path" alt />
            <video v-if="media.type === 'movie'" :src="media.path"></video>
          </button>
          <button
            type="button"
            class="modal-designed-modal-header-image close"
            style="height:100px; width:100px;"
            @click="close"
          >×</button>
        </div>

        <div class="modal-body">
          <div class="row">
            <div class="col-sm-12 modal-designed-modal-right">
              <img
                v-if="selectedMedia.type === 'image'"
                :src="selectedMedia.path"
                class="center-block modal-designed-modal-right-image"
                alt
              />
              <video
                controls
                autoplay
                loop
                v-if="selectedMedia.type === 'movie'"
                :src="selectedMedia.path"
                class="center-block modal-designed-modal-right-image"
              ></video>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    initImages: {
      type: Array,
      required: true
    }
  },
  data: function () {
    return {
      medias: this.initImages,
      selectedMedia: this.initImages[0]
    }
  },
  mounted: function () {
  },
  methods: {
    selectMedia: function (media) {
      this.selectedMedia = media
    },

    close: function () {
      this.$emit('close')
    }
  }
}
</script>

<style lang="scss" scoped>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 90vw;
  height: 90vh;
  margin: 0px auto;
  padding: 20px 0px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header {
  width: 100%;
  min-width: 100%;
  padding: 0;
  overflow-x: auto;

  img, video {
    object-fit: contain;
    width: 100px;
    height: 100px;
  }

  div {
    display: flex;
    margin: 0 auto;
  }
}

.modal-body {
  width: 100%;
  height: 90%;
  margin: 0px auto;
}

.modal-designed-modal-header-image {
  margin: 0 3px;
}
.modal-designed-modal-right {
  height: 100%;
}
.modal-designed-modal-right-image {
  object-fit: contain;
  width: 100%;
  max-height: 73vh;
}
</style>



